.card-container {
  max-width: 400px;
  border: 1px solid #e2e8f0;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
  position: relative;
  padding: 16px;
  transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
}

.card-container:hover {
  transform: scale(1.05);
}
